//sessionStorage
export function getToken() {
  return sessionStorage.getItem(TokenKey)
}

export function setToken(token,time) {
  return sessionStorage.setItem(TokenKey, token)
}

export function removeToken() {
  return sessionStorage.removeItem(TokenKey)
}

export function getUserInfo() {
  return sessionStorage.getItem(UserInfoKey)
}

export function setUserInfo(userInfo,time) {
  return sessionStorage.setItem(UserInfoKey, JSON.stringify(userInfo))
}

export function removeUserInfo() {
  return sessionStorage.removeItem(UserInfoKey)
}
