import { login, logout, getInfo,getjsession ,verifyBanker} from '@/api/user'
import { getToken, setToken, removeToken,getUserInfo,setUserInfo, removeUserInfo } from '@/utils/auth'
import router, { resetRouter } from '@/router'
import store from '../../store'
import Vue from 'vue'



const state = {
  token: getToken(),
  name: '',
  avatar: '',
  introduction: '',
  roles: [],
  ws:{},
  userInfo:getUserInfo()
}

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_USERINFO: (state, userInfo) => {
    state.userInfo = userInfo
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  
    WS(state,wsObj){
    state.ws=wsObj
    }
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password } = userInfo
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password }).then(response => {
        const { data } = response
        if(response.status==0&&data["Bearer "]){
          

          //银行人员必须是总行人员才允许进入页面
          if(data.Power==5){
          commit('SET_TOKEN', data["Bearer "],new Date(msToDate(data.ExpiredAt).hasTime)  )
            verifyBanker({bankerId:data.PersonId}).then(res=>{
              if(res.data==true){
                goNext()
              }else{

            commit('SET_TOKEN', '');
            reject('无权限')
              }
            }).catch(()=>{

            commit('SET_TOKEN', '')
            reject('无权限')
            });
          }else{
          goNext()

          }
          function goNext(){
          commit('SET_TOKEN', data["Bearer "],new Date(msToDate(data.ExpiredAt).hasTime)  )
          commit('SET_USERINFO', JSON.stringify(data) )
          setUserInfo(data,new Date(msToDate(data.ExpiredAt).hasTime));
          setToken(data["Bearer "],new Date(msToDate(data.ExpiredAt).hasTime) );

          // 获取安全云平台登录信息 除了开发人员和用户管理员，其他权限不需要访问这个接口
          if(data.Power==1||data.Power==2||data.Power==8){
            getjsession().then(res=>{
              sessionStorage.jsession = res.jsession;
            });
          }
         
          resolve(data);
          }
        
        }else{
          return Promise.reject(data.Msg)
         
        }
       
      }).catch(error => {
        reject(error)
      })
    })
  },

  // get user info
  getInfo({ commit, state }) {
     return new Promise((resolve, reject) => {
        const { data } = response

        if (!data) {
          reject('Verification failed, please Login again.')
        }

        const { Power } = data
        let roles=[Power];
        // roles must be a non-empty array
        if (!roles || roles.length <= 0) {
          reject('getInfo: roles must be a non-null array!')
        }

        commit('SET_ROLES', roles)
        resolve(data)
     
    })
  },

  // user logout
  logout({ commit, state }) {
    return new Promise((resolve, reject) => {
      logout({username:JSON.parse(getUserInfo()).UserName}).then(() => {
        // 清空本地存储
        //如果有其他的vuex数据，也应在退出时恢复初始状态，添加到这里面
        sessionStorage.clear();
        sessionStorage.setItem(
          "warningData",
          JSON.stringify([])
        );
        sessionStorage.setItem(
          "realTimeWarningData",
          JSON.stringify([])
        );
        sessionStorage.setItem(
          "beidouTerminalMes",
          JSON.stringify([])
        );
        commit('SET_TOKEN', '')
        commit('SET_USERINFO', '[]')
        commit('SET_ROLES', [])
        store.commit('realTimeMonitor/CARANDGUNOBJ', {})
        store.commit('realTimeMonitor/ISADDNEWAlARM', true)
        store.commit('realTimeMonitor/ISEDITFILES', false)
        store.dispatch('realTimeMonitor/RESET_NEWSOCKETDATA')
        store.dispatch('realTimeMonitor/SAVE_WARNINGDATA')
        store.dispatch('realTimeMonitor/SAVE_BEIDOUTERMINALMES')
        store.dispatch('realTimeMonitor/SAVE_REALTIMEWARNINGDATA')
        store.dispatch('realTimeMonitor/RESET_DEVICEONLINE')
        store.dispatch('realTimeMonitor/RESET_DEVICESTATUSRESPONSE')
        store.commit('realTimeMonitor/OFFLINETIMEOUTARR', [])
        store.commit('realTimeMonitor/OFFLINETIMEOUTOBJ', {})
        store.commit('realTimeMonitor/PACKAGEINOUTSOCKET', {})
        store.commit('realTimeMonitor/CARPARKINGDETAILSOCKET', {})
        store.commit('realTimeMonitor/SOCKETTALKDATA', {})
        store.commit('realTimeMonitor/SOCKETTALKDATA', {})
        store.commit('realTimeMonitor/HASCANCLES',false)
        store.commit('realTimeMonitor/FOLLOWCARORGUN',null)
        store.commit('realTimeMonitor/FOLLOWINFO',{})
        removeToken()
        removeUserInfo();
        resetRouter()
        if(JSON.stringify(state.ws) != "{}"){
          state.ws.close()
          state.ws={}
        }
        resolve()
      }).catch(error => {
        reject(error)
      })
    })    
  },

  // remove token
  resetToken({ commit,state }) {
    return new Promise(resolve => {
      // commit('SET_TOKEN', '')
      // commit('SET_USERINFO', '[]')
      // commit('SET_ROLES', [])
      // removeToken()
      // removeUserInfo()
       // 清空本地存储
        //如果有其他的vuex数据，也应在退出时恢复初始状态，添加到这里面

       sessionStorage.clear();
       sessionStorage.setItem(
         "warningData",
         JSON.stringify([])
       );
       sessionStorage.setItem(
         "realTimeWarningData",
         JSON.stringify([])
       );
       sessionStorage.setItem(
         "beidouTerminalMes",
         JSON.stringify([])
       );
       commit('SET_TOKEN', '')
       commit('SET_USERINFO', '[]')
       commit('SET_ROLES', [])
       store.commit('realTimeMonitor/CARANDGUNOBJ', {})
       store.commit('realTimeMonitor/ISADDNEWAlARM', true)
       store.commit('realTimeMonitor/ISEDITFILES', false)
       store.dispatch('realTimeMonitor/RESET_NEWSOCKETDATA')
       store.dispatch('realTimeMonitor/SAVE_WARNINGDATA')
       store.dispatch('realTimeMonitor/SAVE_BEIDOUTERMINALMES')
       store.dispatch('realTimeMonitor/SAVE_REALTIMEWARNINGDATA')
       store.dispatch('realTimeMonitor/RESET_DEVICEONLINE')
       store.dispatch('realTimeMonitor/RESET_DEVICESTATUSRESPONSE')
       store.commit('realTimeMonitor/OFFLINETIMEOUTARR', [])
       store.commit('realTimeMonitor/OFFLINETIMEOUTOBJ', {})
       store.commit('realTimeMonitor/PACKAGEINOUTSOCKET', {})
       store.commit('realTimeMonitor/CARPARKINGDETAILSOCKET', {})
       store.commit('realTimeMonitor/SOCKETTALKDATA', {})
       store.commit('realTimeMonitor/SOCKETTALKDATA', {})
       store.commit('realTimeMonitor/HASCANCLES',false)
       store.commit('realTimeMonitor/FOLLOWCARORGUN',null)
       store.commit('realTimeMonitor/FOLLOWINFO',{})








       removeToken()
       removeUserInfo();
       resetRouter()
       if(JSON.stringify(state.ws) != "{}"){
         state.ws.close()
         state.ws={}
       }
    
      resolve()
    })
  },

  // dynamically modify permissions
  changeRoles({ commit, dispatch }, role) {
    return new Promise(async resolve => {
      const token = role + '-token'
      removeUserInfo()
      commit('SET_TOKEN', token)
      setToken(token)

      const { roles } = await dispatch('getInfo')

      resetRouter()

      // generate accessible routes map based on roles
      const accessRoutes = await dispatch('permission/generateRoutes', roles, { root: true })

      // dynamically add accessible routes
      router.addRoutes(accessRoutes)

      resolve()
    })
  }
}
//转化时间格式用于设置cookie的过期时间
function msToDate (msec) {
  let datetime = new Date(msec);
  let year = datetime.getFullYear();
  let month = datetime.getMonth();
  let date = datetime.getDate();
  let hour = datetime.getHours();
  let minute = datetime.getMinutes();
  let second = datetime.getSeconds();

  let result1 = year + 
               '-' + 
               ((month + 1) >= 10 ? (month + 1) : '0' + (month + 1)) + 
               '-' + 
               ((date + 1) < 10 ? '0' + date : date) + 
               ' ' + 
               ((hour + 1) < 10 ? '0' + hour : hour) +
               ':' + 
               ((minute + 1) < 10 ? '0' + minute : minute) + 
               ':' + 
               ((second + 1) < 10 ? '0' + second : second);

  let result2 = year + 
               '-' + 
               ((month + 1) >= 10 ? (month + 1) : '0' + (month + 1)) + 
               '-' + 
               ((date + 1) < 10 ? '0' + date : date);

  let result = {
      hasTime: result1,
      withoutTime: result2
  };

  return result;
}



export default {
  namespaced: true,
  state,
  mutations,
  actions
}
