import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'

Vue.use(Vuex)

// https://webpack.js.org/guides/dependency-management/#requirecontext
// 导入modules下面所有的js文件 一次引入多个文件        第一个参数: 你要引入文件的目录,第二个参数: 是否要查找该目录下的子级目录 第三个参数: 匹配要引入的文件的规则 返回一个require函数
const modulesFiles = require.context('./modules', false, /\.js$/)
// console.log(modulesFiles.keys()) // kyes属性是一个方法,返回找到的所有的路径数组
// you do not need `import app from './modules/app'`
// it will auto require all vuex module from modules file
const modules = modulesFiles.keys().reduce((modules, modulePath) => {
  // set './app.js' => 'app'
  // 获取文件名
  const moduleName = modulePath.replace(/^\.\/(.*)\.\w+$/, '$1')
  const value = modulesFiles(modulePath) // 获取导入的对象 require(modulePath)
  modules[moduleName] = value.default // 转换为对象
  return modules
}, {})
// console.log(modules) // module {app : require('./app.js)}这样的键值对
const store = new Vuex.Store({
  modules,
  getters
})

export default store
