import axios from 'axios'
import {
    Message,
} from 'element-ui'

import store from '@/store'
import router from './router'


const service = axios.create({
    timeout: 1000*60*2, // request timeout
    baseURL: '/gate'



})
let pending = []; //声明一个数组用于存储每个ajax请求的取消函数和ajax标识
let cancelToken = axios.CancelToken;
let removePending = (config) => {
    for(let p in pending){
        if(pending[p].u === config.url + '&' + config.method) { //当当前请求在数组中存在时执行函数体
            pending[p].f(); //执行取消操作
            pending.splice(p, 1); //把这条记录从数组中移除
        }
    }
}
// 请求拦截
service.interceptors.request.use(
    config => {
        // removePending(config); //在一个ajax发送前执行一下取消操作

        // if(!config.cancelToken){
        //     config.cancelToken = new cancelToken((c)=>{
        //         // 这里的ajax标识我是用请求地址&请求方式拼接的字符串，当然你可以选择其他的一些方式
        //         pending.push({ u: config.url + '&' + config.method, f: c });  
        //     });
        // }
       
        
        if (store.getters.token !== '' && store.getters.token !== undefined) {
            config.headers['Authorization'] = "Bearer " + store.getters.token

        };
        if(config.url.indexOf('StandardApiAction_getVideoFileInfo.action')>-1){// 代理请求
            // config.url=config.url.slice(6);
           return config;
        };
        // 判断如果是云平台，需要移除timeout并且在非登录时加入jsession
        if(config.url.indexOf('/proxy/')>-1){// 代理请求
            // config.timeout = 0;
            if(config.url.indexOf('login')<0){// 不为登录接口
                config.data.jsession = sessionStorage.jsession;
            };
        };
        if (config.url.indexOf('/verify/') < 0&&config.url.indexOf('/noapp/')<0&&config.url.indexOf('/eoms/')<0) { // app模块接口
            if(config.url[0]=='/'){
                config.url='/app'+config.url

            }else{
                config.url='/app/'+config.url
            }
        }else if(config.url.indexOf('/verify/') < 0&&(config.url.indexOf('/noapp/')!==-1)){//电为接口不需要添加/app
            config.url=config.url.slice(6);
        }
        //如果请求头需要为application/json,需要在request请求里面单独设置请求头，其他不用
        if (config.headers['content-type'] == 'application/x-www-form-urlencoded' || config.headers['content-type'] == undefined) {
            //formdata处理
            var formData = new FormData();
            for (var key in config.data) {
                formData.append(key, config.data[key])
            }
            config.data = formData;
        } else if (config.headers['content-type'] == 'application/json') {
            return config;
        }
       
       

        return config;




    },
    error => {
        console.log(error) // for debug
        return Promise.reject(error)
    }
)
// 响应拦截
service.interceptors.response.use(
    response => {
        // removePending(response.config);  //在一个ajax响应后再执行一下取消操作，把已经完成的请求从pending中移除
        let tipMes = "请求错误，请重试";
        const res = response.data;
        if(res.result!=undefined){// 处理云平台返回
            res.status = res.result;
            if (res.status == 1) res.status == 100;// 下边有状态码为1的
            let errMsg = {
                'N0':'请求成功',
                'N100':'用户名不存在',
                'N2':'密码错误',
                'N3':'用户已停用',
                'N4':'用户已过期', 
                'N5':'会话不存在', 
                'N6':'系统出现异常', 
                'N7':'请求参数不正确', 
                'N8':'没有车辆或者设备的操作权限',
                'N9':'开始时间不得大于结束时间', 
                'N10':'查询时间超过范围', 
                'N11':'录像下载任务已存在', 
                'N12':'账号已存在', 
                'N13':'无操作权限', 
                'N14':'设备管理数目限制（已达到最大添加数目）',
                'N15':'设备已存在', 
                'N16':'车辆已存在', 
                'N17':'设备已被使用', 
                'N18':'车辆不存在', 
                'N19':'设备不存在', 
                'N20':'设备不属于当前公司', 
                'N21':'设备注册数目不匹配，请检查设备数目是否超过注册数目！',
                'N24':'网络连接异常', 
                'N25':'规则名称已存在', 
                'N26':'规则不存在', 
                'N27':'信息不存在', 
                'N28':'回话号已存在',
                'N32':'设备不在线',//历史回放搜索视频回放文件时错误码
            };
            res.msg = errMsg[`N${res.result}`];
        };
        //10001在实时监控的车辆tree里面是没有数据的情况，需要让后台改状态码（已提过，暂未改），还有档案管理select接口也出现无数据状态码为10004的情况
        // 增加返回403时的判断
        // res.status=401;
        if ((res.status != 0&& res.code != 200 && res.status != 1 && res.status != 200&& (res.status != undefined||res.code != undefined)) || (res.code == 403)|| (res.status == 403)|| (res.code == 401)|| (res.status == 401)) {
            if (res.code === 403||res.status === 403||res.code === 401||res.status === 401) {
                store.dispatch('user/resetToken')
                router.push({
                    path: "/login"
                });

            return;
            }
            //包含中文
            if(/.*[\u4e00-\u9fa5]+.*$/.test(res.msg)){
                tipMes=res.msg;
            }
            Message({
                // message: res.msg,    
                message: tipMes,
                type: 'error',
                duration: 5 * 1000
            })

            return Promise.reject('error')
        } else {
             //不包含中文
             if(!(/.*[\u4e00-\u9fa5]+.*$/.test(res.msg))){
                res.msg = "操作成功";
            }
            return res
        }
    },
    error => {

       
        let tipMes = "请求错误，请重试";
        // allLoading.close();
        if(axios.isCancel(error)) {  //只拦截取消请求导致的错误信息
            return error;              //这里如果没有return,响应回调函数里的res为undefined
        }
        if(error.response.status==401){
            store.dispatch('user/resetToken')
            router.push({
                path: "/login"
            });

        return;
        }
        //测试服的获取地址接口不报错提示,其他接口报错时均报错提示
        if(!(error.response.config.url.indexOf('thirdparty/gaode/regeo')!==-1&&location.hostname.indexOf('beidoulab.work')!==-1)){
            Message({
                message: tipMes,
                type: 'error',
                duration: 5 * 1000
            })
        }
       

        
        return Promise.reject(error)
    }
)
export default service