

const state={ 
    menu: [],  //菜单

 curentMenu: null //当前点击的菜单

 }

const mutations={

//每次点击的时候，把菜单传入

selectMenu(state, value) {
         console.log(value,'value') //当前点击的菜单
        //判断是否点击到home标签
         if (value.name !== 'home') {
             state.curentMenu = value
         } else {
             state.curentMenu = null;
         }
     }
 
}
const actions = {
   
  }

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
