import router from './router'
import store from './store'
import {
  Message
} from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import {
  getToken,
  getUserInfo
} from '@/utils/auth' // get token from cookie
// import getPageTitle from '@/utils/get-page-title'

NProgress.configure({
  showSpinner: false
}) // NProgress Configuration

const whiteList = ['/login'] // no redirect whitelist

router.beforeEach(async (to, from, next) => {
  // start progress bar
  NProgress.start()

  // set page title
  //   document.title = getPageTitle(to.meta.title)

  // determine whether the user has logged in
  const hasToken = getToken()
  // console.log(getUserInfo())

 
  if (hasToken) {
    if (to.path === '/login') {//如果存在token,并且要跳转到登录页面，则重定向到首页
      next({ path: '/' })
      NProgress.done()
    } else {
      //判断是否有角色信息
      const hasRoles = store.getters.roles && store.getters.roles.length > 0 
      // console.log(hasRoles)
      if (hasRoles) {
        next()
      } else {
        try {
          // get user info
          // note: roles must be a object array! such as: ['admin'] or ,['developer','editor']
          // const { roles } = await store.dispatch('user/getInfo')//获取用户信息，并得到用户角色，roles为一个数组，如['developer','editor']
          var roles=[];
          if(getUserInfo()){
            roles.push(JSON.parse(getUserInfo()).Power)
          }
         
          await store.commit('user/SET_ROLES',roles)
          // generate accessible routes map based on roles
          const accessRoutes = await store.dispatch('permission/generateRoutes', roles)//获取跟roles比对以后可用于挂载的路由

          // dynamically add accessible routes
          router.addRoutes(accessRoutes)//挂载路由

          // hack method to ensure that addRoutes is complete
          // set the replace: true, so the navigation will not leave a history record
          next({ ...to, replace: true })
        } catch (error) {
         
          // remove token and go to login page to re-login
          await store.dispatch('user/resetToken')
          Message.error(error || 'Has Error')
          next(`/login?redirect=${to.path}`)
          NProgress.done()
        }
      } 
    }
  } else {
    /* has no token*/

    if (whiteList.indexOf(to.path) !== -1) {
      // in the free login whitelist, go directly
      next()
    } else {
      // other pages that do not have permission to access are redirected to the login page.
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }

  
})

router.afterEach(() => {
  // finish progress bar
  NProgress.done()
})